@import url("https://fonts.googleapis.com/css?family=Sawarabi+Mincho&display=swap");
@import url("https://fonts.googleapis.com/css?family=Playfair+Display:400,900&display=swap");
@mixin flexbox {
  display: -webkit-box;
  display: -moz-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
}

@mixin flexVstart {
  align-content: flex-start;
  -webkit-align-content: flex-start;
  -webkit-box-align: flex-start;
  -ms-flex-align: flex-start;
  -ms-align-items: flex-start;
  align-items: flex-start;
}

@mixin flexVcenter {
  align-content: center;
  -webkit-align-content: center;
  -webkit-box-align: center;
  -moz-box-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  -ms-align-items: center;
  align-items: center;
}

@mixin flexVEnd {
  align-content: flex-end;
  -webkit-align-content: flex-end;
  -webkit-box-align: flex-end;
  -moz-box-align: flex-end;
  -ms-flex-align: flex-end;
  -webkit-align-items: flex-end;
  -ms-align-items: flex-end;
  align-items: flex-end;
}

@mixin flexJustifyCenter {
  -webkit-box-pack: center;
  -moz-box-pack: center;
  -ms-flex-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
}

@mixin flexWrap {
  flex-wrap: wrap;
  -ms-flex-wrap: wrap;
}

@mixin flexDirectionColumn {
  -webkit-flex-direction: column;
  flex-direction: column;
}

@mixin flexDirectionReverse {
  -webkit-flex-direction: row-reverse;
  flex-direction: row-reverse;
}

@mixin flexDirectionColReverse {
  -webkit-flex-direction: column-reverse;
  flex-direction: column-reverse;
}

@mixin flexSpaceBetween {
  -webkit-box-pack: justify;
  -moz-box-pack: justify;
  -ms-flex-pack: justify;
  -webkit-justify-content: space-between;
  justify-content: space-between;
}

@mixin flexSpaceAround {
  -webkit-box-pack: justify;
  -moz-box-pack: justify;
  -ms-flex-pack: justify;
  -webkit-justify-content: space-around;
  justify-content: space-around;
}

@mixin flexJustifyEnd {
  justify-content: flex-end;
  -webkit-justify-content: flex-end;
  -webkit-box-pack: end;
  -moz-box-pack: end;
  -ms-flex-pack: end;
}

@mixin flexJustifyStart {
  justify-content: flex-start;
  -webkit-justify-content: flex-start;
  -webkit-box-pack: start;
  -moz-box-pack: start;
  -ms-flex-pack: start;
}

//=======================================================================
$mobile: 768px;
$smallMobile: 480px;
$red: rgb(199, 0, 37);

@mixin transitionCubic {
  -moz-transition: all 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  -o-transition: all 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  -webkit-transition: all 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  transition: all 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}

@mixin rotate($degrees) {
  -webkit-transform: rotate($degrees);
  -moz-transform: rotate($degrees);
  -ms-transform: rotate($degrees);
  -o-transform: rotate($degrees);
  transform: rotate($degrees);
}

body {
  margin: 0;
  font-family: "Sawarabi Mincho", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 16px;
  line-height: 1.3;
  color: #000000;
}
:focus {
  outline: none;
}

a:link,
a:visited {
  color: #000;
  text-decoration: underline;
}
.App {
  @include flexbox;
  @include flexJustifyCenter;
}

h1 {
  font-size: 100px;
  width: 100%;
  letter-spacing: 0.1em;
  font-weight: bold;
  line-height: 1;
  margin: 40px auto 20px;
  text-align: center;
  &.main-head {
    letter-spacing: -2px;
    line-height: 1em;
    margin-top: 0;
    margin-bottom: 50px;
    text-decoration: underline;
  }
}
h2,
h3 {
  font-family: "Playfair Display", serif;
  font-weight: 900;
}
header {
  position: fixed;
  display: block;
  z-index: 999;
  top: 0;
  left: 0;
  width: 100vw;
  height: 80px;
  background: #fff;
  border-bottom: 3px solid #000;
  color: #000;
  font-weight: bold;
  ul {
    width: 100%;
    height: 100%;
    @include flexbox;
    @include flexVcenter;
    @include flexJustifyCenter;
    padding: 0;
    margin: 0;
    li {
      list-style: none;
      display: inline-block;
      position: relative;
      margin: 0 10px;
      a {
        z-index: 5;
        position: absolute;
        top: -15px;
        left: 0px;
        width: 40px;
        height: 55px;
        text-align: center;
      }
      h1 {
        font-size: 30px;
        width: auto;
        margin: 0;
      }
      img {
        width: 40px;
        display: inline-block;
      }
    }
  }
}
.main-scene {
  padding-top: 80px;
  width: 100%;
  max-width: 850px;
  overflow: hidden;
  margin: 0 auto;
  background: #ffffff;
}
section {
  position: relative;
  padding: 15px;
  padding-bottom: 30px;
  &:after {
    width: 90vw;
    background: #000;
    height: 1px;
    content: "";
    bottom: 0;
    display: inline-block;
    position: absolute;
  }
}
.flight {
  &-info {
    width: 100%;
    @include flexbox;
    @include flexVcenter;
    margin: 0px auto 50px;
    h1 {
      display: inline-block;
      position: relative;
      width: auto;
      margin: 0 50px 0 0;
      font-size: 80px;
      &:after {
        position: absolute;
        content: "";
        background: #000;
        height: 1.6em;
        width: 2px;
        top: 20px;
        right: -4px;
        @include rotate(20deg);
      }
      // font-weight: normal;
    }
  }
}
.weather-info {
  margin: 80px auto 20px;
  width: 100%;
  @include flexbox;
  @include flexSpaceAround;
  @include flexVcenter;
  &--day {
    @include flexbox;
    @include flexDirectionColumn;
    @include flexJustifyCenter;
    width: 20%;
    position: relative;
    padding-right: 2px;
    &:after {
      display: block;
      position: absolute;
      width: 1px;
      height: 100%;
      right: 0;
      top: 0;
      content: "";
      background: rgba(0, 0, 0, 0.6);
    }
    &:last-child {
      &:after {
        display: none;
      }
    }
    p {
      font-size: 14px;
      text-align: center;
      margin: 0 auto 18px;
    }
    img {
      width: 20px;
      margin: 0 auto;
    }
    .temp {
      width: 100%;
      text-align: center;
      .max {
        color: $red;
      }
    }
  }
}
.slash-end {
  display: inline-block;
  position: relative;
  width: auto;
  font-size: 20px;
  margin-right: 30px;
  &:after {
    position: absolute;
    content: "";
    background: #000;
    height: 23px;
    width: 1px;
    top: 5px;
    right: -10px;
    @include rotate(20deg);
  }
}
.full {
  width: 100%;
  margin: 0px auto 10px;
  line-height: 1.8;
  @include flexbox;
  @include flexWrap;
  @include flexVcenter;
}
.center {
  text-align: center;
}
.time-wrapper {
  @include flexbox;
  @include flexWrap;
  margin-bottom: 30px;
  position: relative;
  &:before {
    content: "";
    display: block;
    height: calc(100% + 25px);
    width: 1px;
    background: #000;
    position: absolute;
    left: 13px;
    top: 20px;
  }
  .time-mark {
    position: relative;
    padding-left: 45px;
    line-height: 35px;
    font-size: 40px;
    letter-spacing: -5px;
    .small-time {
      font-size: 18px !important;
      letter-spacing: normal;
    }
    &.nested {
      line-height: 1em;
    }
    &:before {
      display: block;
      position: absolute;
      left: 0px;
      top: 4px;
      content: "";
      background: #000;
      width: 30px;
      height: 30px;
      border-radius: 50%;
    }
    &.click {
      cursor: pointer;
      svg {
        width: 30px;
        height: 30px;
        width: 30px;
        height: 30px;
        position: absolute;
        left: 12px;
        top: 10px;
        @include rotate(-20deg);
      }
    }
  }
}
ul {
  padding-left: 20px;
}
li {
  margin-bottom: 20px;
  p {
    margin-bottom: 10px;
    margin-top: 0;
  }
}
.time-detail {
  width: calc(100% - 25px);
  padding-left: 25px;

  .slash-end {
    font-size: 16px;
  }
  .full {
    font-size: 14px;
    .place-target {
      font-size: 14px;
      font-weight: normal;
      margin: 0;
    }
  }
}
.place-target {
  font-size: 22px;
  font-weight: bold;
  width: 100%;
  margin: 16px 0;
  span {
    display: inline-block;
    position: relative;
    padding-right: 30px;
    &:after {
      position: absolute;
      display: block;
      width: 22px;
      height: 1px;
      background: #000;
      right: 4.5px;
      top: 10px;
      content: "";
    }
    &:last-of-type:after {
      display: none;
    }
  }
}
.remark {
  font-size: 0.85em;
  color: $red;
}
.map {
  width: 100%;
  height: 350px;
  position: relative;
}
.img-wrapper {
  @include flexbox;
  @include flexWrap;
  @include flexSpaceAround;
  margin: 15px 0;
  @media (max-width: $mobile) {
    display: block;
  }
  img {
    width: 45%;
    object-fit: cover;
    margin-bottom: 15px;
    height: auto;
    max-height: 400px;
    @media (max-width: $mobile) {
      width: 100%;
      height: auto;
    }
  }
}
.jpicon {
  width: 30px;
  margin-right: 15px;
}
.highlight {
  background: #000;
  color: #fff !important;
  display: inline-block;
  padding: 3px;
}
a.highlight {
  position: relative;
  text-decoration: none;
  padding-left: 20px;
  &:before {
    content: ">";
    position: absolute;
    left: 5px;
    margin: 0 3px;
    font-size: 0.9em;
  }
}
.narrow-highlight {
  background: #000;
  color: #fff !important;
  display: inline-block;
  padding: 3px;
  line-height: 1;
  padding-right: 3px !important;
  &.orange {
    background: #ff8c00;
  }
  &.lightblue {
    background: #41b3d5;
  }
  &.midblue {
    background: #00aeef;
  }
  &.darkblue {
    background: #21448e;
  }
  &.lightgreen {
    background: #73c11d;
  }
}
.content-img {
  width: 100%;
}
.caption-img {
  width: 100%;
  img {
    width: 100%;
  }
  p {
    margin: 0 0 3px 0;
    width: 100%;
    text-align: center;
  }
}
.iframe {
  width: 100%;
  iframe {
    width: 100%;
  }
}
