@import url(https://fonts.googleapis.com/css?family=Sawarabi+Mincho&display=swap);
@import url(https://fonts.googleapis.com/css?family=Playfair+Display:400,900&display=swap);
body {
  margin: 0;
  font-family: "Sawarabi Mincho", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 16px;
  line-height: 1.3;
  color: #000000; }

:focus {
  outline: none; }

a:link,
a:visited {
  color: #000;
  text-decoration: underline; }

.App {
  display: flex;
  justify-content: center; }

h1 {
  font-size: 100px;
  width: 100%;
  letter-spacing: 0.1em;
  font-weight: bold;
  line-height: 1;
  margin: 40px auto 20px;
  text-align: center; }
  h1.main-head {
    letter-spacing: -2px;
    line-height: 1em;
    margin-top: 0;
    margin-bottom: 50px;
    text-decoration: underline; }

h2,
h3 {
  font-family: "Playfair Display", serif;
  font-weight: 900; }

header {
  position: fixed;
  display: block;
  z-index: 999;
  top: 0;
  left: 0;
  width: 100vw;
  height: 80px;
  background: #fff;
  border-bottom: 3px solid #000;
  color: #000;
  font-weight: bold; }
  header ul {
    width: 100%;
    height: 100%;
    display: flex;
    align-content: center;
    -webkit-align-content: center;
    -ms-align-items: center;
    align-items: center;
    justify-content: center;
    padding: 0;
    margin: 0; }
    header ul li {
      list-style: none;
      display: inline-block;
      position: relative;
      margin: 0 10px; }
      header ul li a {
        z-index: 5;
        position: absolute;
        top: -15px;
        left: 0px;
        width: 40px;
        height: 55px;
        text-align: center; }
      header ul li h1 {
        font-size: 30px;
        width: auto;
        margin: 0; }
      header ul li img {
        width: 40px;
        display: inline-block; }

.main-scene {
  padding-top: 80px;
  width: 100%;
  max-width: 850px;
  overflow: hidden;
  margin: 0 auto;
  background: #ffffff; }

section {
  position: relative;
  padding: 15px;
  padding-bottom: 30px; }
  section:after {
    width: 90vw;
    background: #000;
    height: 1px;
    content: "";
    bottom: 0;
    display: inline-block;
    position: absolute; }

.flight-info {
  width: 100%;
  display: flex;
  align-content: center;
  -webkit-align-content: center;
  -ms-align-items: center;
  align-items: center;
  margin: 0px auto 50px; }
  .flight-info h1 {
    display: inline-block;
    position: relative;
    width: auto;
    margin: 0 50px 0 0;
    font-size: 80px; }
    .flight-info h1:after {
      position: absolute;
      content: "";
      background: #000;
      height: 1.6em;
      width: 2px;
      top: 20px;
      right: -4px;
      -webkit-transform: rotate(20deg);
      transform: rotate(20deg); }

.weather-info {
  margin: 80px auto 20px;
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-content: center;
  -webkit-align-content: center;
  -ms-align-items: center;
  align-items: center; }
  .weather-info--day {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 20%;
    position: relative;
    padding-right: 2px; }
    .weather-info--day:after {
      display: block;
      position: absolute;
      width: 1px;
      height: 100%;
      right: 0;
      top: 0;
      content: "";
      background: rgba(0, 0, 0, 0.6); }
    .weather-info--day:last-child:after {
      display: none; }
    .weather-info--day p {
      font-size: 14px;
      text-align: center;
      margin: 0 auto 18px; }
    .weather-info--day img {
      width: 20px;
      margin: 0 auto; }
    .weather-info--day .temp {
      width: 100%;
      text-align: center; }
      .weather-info--day .temp .max {
        color: #c70025; }

.slash-end {
  display: inline-block;
  position: relative;
  width: auto;
  font-size: 20px;
  margin-right: 30px; }
  .slash-end:after {
    position: absolute;
    content: "";
    background: #000;
    height: 23px;
    width: 1px;
    top: 5px;
    right: -10px;
    -webkit-transform: rotate(20deg);
    transform: rotate(20deg); }

.full {
  width: 100%;
  margin: 0px auto 10px;
  line-height: 1.8;
  display: flex;
  flex-wrap: wrap;
  -ms-flex-wrap: wrap;
  align-content: center;
  -webkit-align-content: center;
  -ms-align-items: center;
  align-items: center; }

.center {
  text-align: center; }

.time-wrapper {
  display: flex;
  flex-wrap: wrap;
  -ms-flex-wrap: wrap;
  margin-bottom: 30px;
  position: relative; }
  .time-wrapper:before {
    content: "";
    display: block;
    height: calc(100% + 25px);
    width: 1px;
    background: #000;
    position: absolute;
    left: 13px;
    top: 20px; }
  .time-wrapper .time-mark {
    position: relative;
    padding-left: 45px;
    line-height: 35px;
    font-size: 40px;
    letter-spacing: -5px; }
    .time-wrapper .time-mark .small-time {
      font-size: 18px !important;
      letter-spacing: normal; }
    .time-wrapper .time-mark.nested {
      line-height: 1em; }
    .time-wrapper .time-mark:before {
      display: block;
      position: absolute;
      left: 0px;
      top: 4px;
      content: "";
      background: #000;
      width: 30px;
      height: 30px;
      border-radius: 50%; }
    .time-wrapper .time-mark.click {
      cursor: pointer; }
      .time-wrapper .time-mark.click svg {
        width: 30px;
        height: 30px;
        width: 30px;
        height: 30px;
        position: absolute;
        left: 12px;
        top: 10px;
        -webkit-transform: rotate(-20deg);
        transform: rotate(-20deg); }

ul {
  padding-left: 20px; }

li {
  margin-bottom: 20px; }
  li p {
    margin-bottom: 10px;
    margin-top: 0; }

.time-detail {
  width: calc(100% - 25px);
  padding-left: 25px; }
  .time-detail .slash-end {
    font-size: 16px; }
  .time-detail .full {
    font-size: 14px; }
    .time-detail .full .place-target {
      font-size: 14px;
      font-weight: normal;
      margin: 0; }

.place-target {
  font-size: 22px;
  font-weight: bold;
  width: 100%;
  margin: 16px 0; }
  .place-target span {
    display: inline-block;
    position: relative;
    padding-right: 30px; }
    .place-target span:after {
      position: absolute;
      display: block;
      width: 22px;
      height: 1px;
      background: #000;
      right: 4.5px;
      top: 10px;
      content: ""; }
    .place-target span:last-of-type:after {
      display: none; }

.remark {
  font-size: 0.85em;
  color: #c70025; }

.map {
  width: 100%;
  height: 350px;
  position: relative; }

.img-wrapper {
  display: flex;
  flex-wrap: wrap;
  -ms-flex-wrap: wrap;
  justify-content: space-around;
  margin: 15px 0; }
  @media (max-width: 768px) {
    .img-wrapper {
      display: block; } }
  .img-wrapper img {
    width: 45%;
    object-fit: cover;
    margin-bottom: 15px;
    height: auto;
    max-height: 400px; }
    @media (max-width: 768px) {
      .img-wrapper img {
        width: 100%;
        height: auto; } }

.jpicon {
  width: 30px;
  margin-right: 15px; }

.highlight {
  background: #000;
  color: #fff !important;
  display: inline-block;
  padding: 3px; }

a.highlight {
  position: relative;
  text-decoration: none;
  padding-left: 20px; }
  a.highlight:before {
    content: ">";
    position: absolute;
    left: 5px;
    margin: 0 3px;
    font-size: 0.9em; }

.narrow-highlight {
  background: #000;
  color: #fff !important;
  display: inline-block;
  padding: 3px;
  line-height: 1;
  padding-right: 3px !important; }
  .narrow-highlight.orange {
    background: #ff8c00; }
  .narrow-highlight.lightblue {
    background: #41b3d5; }
  .narrow-highlight.midblue {
    background: #00aeef; }
  .narrow-highlight.darkblue {
    background: #21448e; }
  .narrow-highlight.lightgreen {
    background: #73c11d; }

.content-img {
  width: 100%; }

.caption-img {
  width: 100%; }
  .caption-img img {
    width: 100%; }
  .caption-img p {
    margin: 0 0 3px 0;
    width: 100%;
    text-align: center; }

.iframe {
  width: 100%; }
  .iframe iframe {
    width: 100%; }

